  methods:{
    searchPage:function(arg){
      console.log(arg)
    },

    audioDataFun:function(id,page){
      let instance = Toast({
        message: '',
        iconClass: 'icon icon-success',
        duration:-1,
      });

      var url='https://api.imjad.cn/qqfm/v1/?type=album&page_size=20&id='+id+'&page='+page;

      this.$ajax.get(url).then((res)=>{
          //console.log(res.status,res.data)
          if(res.status==200){
            instance.close();
            this.audioListArr=res.data.data.albumInfoList;
            if(res.data.data.total){
              this.total=res.data.data.total
            }else{
              this.total=0
            }
            
          }else{
            instance.close();
            Toast({
              message: '',
              iconClass: 'icon icon-success',
              
            });
          }
          
        }).catch((res)=>{
          console.log(res)
        });
    },


classtypeFun:function(id){
  //console.log(id)
  this.classId=id;
  this.childClassId='',
  this.pageNum=1;
  this.audioDataFun(id,1)
},

classChildFun:function(id){
  this.pageNum=1;
  this.childClassId=id;

  this.audioDataFun(id,1)
},

upPageFun:function(){
  if(this.pageNum>1){
    this.pageNum--;
    let page=this.pageNum;
    var id='';
    if(this.childClassId){
      id=this.childClassId
    }else{
      id=this.classId
    }
    this.audioDataFun(id,page)
  }else{
      Toast({
        message: '',
        iconClass: 'icon icon-success',
        
      });
  }

},
downPageFun:function(){
  if(this.pageNum>=this.total){
      Toast({
        message: '',
        iconClass: 'icon icon-success',
      });
  }else{
      this.pageNum++;
      let page=this.pageNum;
      var id='';
      if(this.childClassId){
        id=this.childClassId
      }else{
        id=this.classId
      }
      this.audioDataFun(id,page)
  }
},


jumpPageFun:function(id){
  this.$router.push({
    path:'./audiodegist',
    query:{id:id}
  })
},
searchPage:function(id){
    this.$router.push({
    path:'./searchpage',
    query:{id:id}
  })
},









